"use client";

import Link from "next/link";
import { useRef } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { LiftingFusionBlock } from "./LiftingFusionBlock";

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);

const LIFTING_TREATMENTS = [
  {
    id: "ulthera",
    name: "울쎄라 (Ulthera)",
    link: "/signature/ulthera",
    description:
      "마이크로포커스 초음파(MFUS) 기술을 사용하여 피부 표면에 손상을 주지 않으면서 SMAS층(근건막층)까지 에너지를 전달하는 FDA 승인 시술입니다. DeepSEE™ 기술로 실시간 초음파 영상을 통해 피부 깊이를 확인하며 정확하게 시술합니다.",
  },
  {
    id: "shrink",
    name: "슈링크 유니버스 (Shrink Universe)",
    link: "#",
    description:
      "고강도 집속 초음파(HIFU) 에너지를 이용한 2세대 리프팅 시술입니다. MP 모드와 Normal(도트) 모드 두 가지 모드로 시술이 가능하며, 총 7가지 카트리지로 깊이 맞춤화(1.5mm~4.5mm)가 가능합니다.",
  },
  {
    id: "tune",
    name: "튠페이스 (Tune Face)",
    link: "#",
    description:
      "이스라엘 ALMA사에서 개발한 멀티 고주파를 활용한 페이스리프팅 시술로, 40.68MHz의 이상적인 주파수 에너지를 사용하여 피부 속 물분자를 회전시키고, 이로 인한 마찰열로 콜라겐을 자극하고 재생합니다.",
  },
];

const SPECIALITY_ITEMS = [
  {
    id: "effect01",
    title: "높은 고정력과 리프팅효과",
    description: "각 리프팅 시술을 기술적으로 컴바인하여 단 1일만에 원하는 리프팅 효과를 나타낼 수 있습니다.",
  },
  {
    id: "effect02",
    title: "빠른 시술 속도",
    description: "효율적인 시술 방법으로 바쁜 시간 중에도 편안하게 부담없이 받으실 수 있습니다.",
  },
  {
    id: "effect03",
    title: "시술 후 통증 최소화",
    description: "정확한 피부층에 에너지를 전달하면서 빠르게 완성하여 붓기와 멍, 통증이 거의 없는 방법으로 시술하고 있습니다.",
  },
  {
    id: "effect04",
    title: "얼굴형 맞춤시술",
    description: "개인의 골격, 피부 두께, 근육의 움직임까지 분석하여 가장 이상적인 리프팅 벡터를 설계합니다.",
  },
  {
    id: "effect05",
    title: "편안한 시술",
    description: "리프팅에 대한 걱정과 두려움을 최소화하여 편안하게 시술 받으실 수 있습니다.",
  },
];

const FEATURE_ITEMS = [
  {
    num: "01",
    title: "울쎄라 (Ulthera)",
    description: "마이크로포커스 초음파 기술로 SMAS층까지 에너지를 전달하는 FDA 승인 시술",
    link: "/signature/ulthera",
  },
  {
    num: "02",
    title: "슈링크 유니버스",
    description: "고강도 집속 초음파(HIFU) 에너지를 이용한 2세대 리프팅 시술",
    link: "#",
  },
  {
    num: "03",
    title: "튠페이스",
    description: "멀티 고주파를 활용한 페이스리프팅 시술로 3단계 깊이 조절이 가능",
    link: "#",
  },
];

export default function LiftingView() {
  const mainRef = useRef<HTMLElement>(null);
  const wrapRef = useRef<HTMLDivElement>(null);

  useGSAP(
    () => {
      // 수평 스크롤 (데스크톱)
      ScrollTrigger.matchMedia({
        "(min-width: 1025px)": () => {
          if (!wrapRef.current) return;

          const horizon = gsap.to("._lifting .horizontal .Wrap", {
            xPercent: -100,
            x: () => window.innerWidth,
            scrollTrigger: {
              pin: "._lifting .horizontal",
              trigger: "._lifting .horizontal",
              start: "left left",
              end: () => `+=${wrapRef.current?.offsetWidth || 0} bottom`,
              scrub: 1,
            },
          });

          // sec1 애니메이션
          gsap.timeline()
            .from("._lifting .sec1 .bg", { scale: 1.2, duration: 2 })
            .from("._lifting .sec1 .lbx", { opacity: 0, xPercent: -100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .rbx", { opacity: 0, xPercent: 100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .tit p", { opacity: 0, y: 40, stagger: 0.2, duration: 0.8 }, "-=80%");

          // sec2 애니메이션
          const tl2 = gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec2",
              start: "left 45%",
              end: "left",
              containerAnimation: horizon,
              toggleActions: "play none none reverse",
            },
          });
          tl2
            .from("._lifting .sec2 .bg", { scale: 1.2, duration: 1.5 })
            .from("._lifting .sec2 .tit h2", { opacity: 0, duration: 0.8, y: 40 }, "-=100%")
            .from("._lifting .sec2 .tit .bx", { opacity: 0, duration: 0.8, y: 40 }, "-=80%");

          // 헤더 색상 변경
          ScrollTrigger.create({
            trigger: "._lifting .sec2",
            start: "left 45%",
            containerAnimation: horizon,
            onEnter: () => {
              document.querySelector("header")?.classList.remove("white");
            },
            onLeaveBack: () => {
              document.querySelector("header")?.classList.add("white");
            },
          });

          // sec3 애니메이션
          const tl3 = gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec3",
              start: "left 90%",
              end: "left",
              containerAnimation: horizon,
              toggleActions: "play none none reverse",
            },
          });
          tl3
            .from("._lifting .sec3 .con .tit > *", { opacity: 0, stagger: 0.2, duration: 0.8, x: -40 })
            .from("._lifting .sec3 .con .sub p > *", { opacity: 0, stagger: 0.2, duration: 0.8, y: 40 }, "-=80%");

          // 마퀴 애니메이션
          setTimeout(() => {
            const marquees = document.querySelectorAll("._lifting .sec2 .marquee .absol");
            marquees.forEach((marquee) => {
              const marqueeChild = marquee.querySelector(".mar_ch");
              if (!marqueeChild) return;

              const cloneTimes = Math.ceil(
                (marquee as HTMLElement).getBoundingClientRect().width /
                  (marqueeChild as HTMLElement).getBoundingClientRect().width
              );

              for (let i = 0; i < cloneTimes; i++) {
                const clone = marqueeChild.cloneNode(true);
                marquee.appendChild(clone);
              }

              gsap.fromTo(
                marquee.querySelectorAll(".mar_ch"),
                { xPercent: 0 },
                {
                  xPercent: -100,
                  repeat: -1,
                  duration: 15,
                  ease: "none",
                }
              );
            });
          }, 100);
        },
        "(max-width: 1024px)": () => {
          // 모바일 애니메이션
          gsap.timeline()
            .from("._lifting .sec1 .bg", { scale: 1.2, duration: 2 })
            .from("._lifting .sec1 .lbx", { opacity: 0, xPercent: -100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .rbx", { opacity: 0, xPercent: 100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .tit p", { opacity: 0, y: 40, stagger: 0.2, duration: 0.8 }, "-=80%");

          gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec2",
              start: "top center+=20%",
              toggleActions: "play none none reverse",
            },
          })
            .from("._lifting .sec2 .bg", { scale: 1.2, duration: 1.5 })
            .from("._lifting .sec2 .tit h2", { opacity: 0, duration: 0.8, y: 40 }, "-=100%")
            .from("._lifting .sec2 .tit .bx", { opacity: 0, duration: 0.8, y: 40 }, "-=80%");

          gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec3",
              start: "top center+=20%",
              toggleActions: "play none none reverse",
            },
          })
            .from("._lifting .sec3 .con .tit > *", { opacity: 0, stagger: 0.2, duration: 0.8, x: -40 })
            .from("._lifting .sec3 .con .sub p > *", { opacity: 0, stagger: 0.2, duration: 0.8, y: 40 }, "-=80%");
        },
      });

      // Speciality 섹션
      ScrollTrigger.matchMedia({
        "(min-width: 1025px)": () => {
          ScrollTrigger.create({
            trigger: "._lifting .speciality .lbx",
            endTrigger: "._lifting .speciality",
            start: "top top",
            end: "bottom bottom",
            scrub: 1,
            pin: true,
          });

          gsap.to("._lifting .speciality .wrap", {
            scrollTrigger: {
              trigger: "._lifting .speciality .bg",
              endTrigger: "._lifting .speciality",
              start: "top top",
              end: "bottom bottom",
              scrub: 1,
              pin: true,
            },
          });
        },
      });

      const specialityTl = gsap.timeline({
        scrollTrigger: {
          trigger: "._lifting .speciality",
          start: "top center+=20%",
          toggleActions: "play none none reverse",
          onEnter: () => {
            document.querySelector("header")?.classList.remove("white");
          },
          onLeaveBack: () => {
            document.querySelector("header")?.classList.add("white");
          },
        },
      });
      specialityTl
        .from("._lifting .speciality .wrap .tit h2", { opacity: 0, x: 40, duration: 0.8 })
        .from("._lifting .speciality .lbx", { opacity: 0, x: -40, duration: 0.8 }, "-=80%");

      const infoBoxes = document.querySelectorAll("._lifting .speciality .con .rbx .bx");
      infoBoxes.forEach((item) => {
        gsap.timeline({
          scrollTrigger: {
            trigger: item,
            start: "top center+=20%",
            toggleActions: "play none none reverse",
          },
        }).from(item, { opacity: 0, y: 40, duration: 0.8 });
      });

      // Feature 섹션
      gsap.timeline({
        scrollTrigger: {
          trigger: "._lifting .feature",
          start: "top center+=20%",
          toggleActions: "play none none reverse",
        },
      })
        .from("._lifting .feature .tit h2", { opacity: 0, y: 40, duration: 0.8 })
        .from("._lifting .feature .tit p", { opacity: 0, y: 40, duration: 0.8 }, "-=80%");

      const featureBoxes = document.querySelectorAll("._lifting .feature .con .bx");
      featureBoxes.forEach((item, idx) => {
        gsap.timeline({
          scrollTrigger: {
            trigger: item,
            start: "top center+=20%",
            toggleActions: "play none none reverse",
          },
        }).from(item, { opacity: 0, y: 40, duration: 0.8, delay: (idx % 2) * 0.1 });
      });
    },
    { scope: mainRef, dependencies: [] }
  );

  return (
    <main ref={mainRef} className="_lifting _s min-h-screen bg-[#fafafa] text-[#111]">
      {/* 수평 스크롤 섹션 */}
      <section className="horizontal relative w-full overflow-hidden">
        <div ref={wrapRef} className="Wrap relative flex w-max">
          {/* 섹션 1: 히어로 */}
          <div className="sec1 relative h-screen w-screen flex-shrink-0 flex items-center justify-center overflow-hidden bg-[#1a1a1a]">
            <div
              className="bg absolute inset-0 bg-cover bg-center bg-[#1a1a1a]"
              style={{
                backgroundImage: "url(https://aanclinic.com/img/main_img01.jpg)",
              }}
            />
            <div className="lbx absolute left-[10%] top-1/2 -translate-y-1/2 z-10">
              <span className="text-[clamp(60px,12vw,120px)] font-light tracking-[0.2em] text-white" style={{ fontFamily: 'var(--font-en-grotesk)' }}>
                FIGURE
              </span>
            </div>
            <div className="rbx absolute right-[10%] top-1/2 -translate-y-1/2 z-10">
              <span className="text-[clamp(60px,12vw,120px)] font-light tracking-[0.2em] text-white" style={{ fontFamily: 'var(--font-en-grotesk)' }}>
                MAKING
              </span>
            </div>
            <div className="tit absolute bottom-[15%] left-1/2 -translate-x-1/2 z-10 text-center max-w-4xl px-8">
              <p className="text-[clamp(16px,2vw,20px)] text-white leading-relaxed mb-4">
                아름다운 라인을 만드는 "FIGURE MAKING"을 모토로 탄생한
                <br />
                오마쥬의 TOP 시그니처 시술.
              </p>
              <p className="text-[clamp(14px,1.5vw,18px)] text-white/90 leading-relaxed">
                얼굴의 전반적인 조화, 자연스러움, 각각의 개성을 살리고
                <br />
                단점을 보완하는 방향으로 수술없이 간단한 시술로도
                <br />
                본연의 숨겨진 라인과 아름다움을 재발견.
              </p>
            </div>
            <div className="scrollcircle absolute bottom-20 left-1/2 -translate-x-1/2 z-10">
              <div className="rel relative">
                <img 
                  src="/asset/img/sub/scroll_circle.png" 
                  alt="scroll" 
                  className="w-16 h-16 opacity-50"
                  onError={(e) => {
                    (e.target as HTMLImageElement).style.display = "none";
                  }}
                />
                <span className="absolute inset-0 flex items-center justify-center">
                  <svg
                    id="arrow_down"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 128.66 218.52"
                    className="w-8 h-8"
                  >
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,408.4a61.33,61.33,0,0,1,61.33-61.33"
                      transform="translate(610.96 -192.89)"
                    />
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,195.89V408.4A61.33,61.33,0,0,0-608,347.07"
                      transform="translate(610.96 -192.89)"
                    />
                  </svg>
                </span>
              </div>
            </div>
          </div>

          {/* 섹션 2: 타이틀 */}
          <div className="sec2 relative h-screen w-screen flex-shrink-0 flex items-center justify-center overflow-hidden bg-[#1a1a1a]">
            <div
              className="bg absolute inset-0 bg-cover bg-center bg-[#1a1a1a]"
              style={{
                backgroundImage: "url(https://www.blsclinic.com/common/img/package/ulthera/pc/common/ulthera_img1.png)",
              }}
            />
            <div className="tit absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 z-10 text-center">
              <h2 className="text-[clamp(60px,12vw,120px)] font-light tracking-[0.1em] text-white mb-6" style={{ fontFamily: 'var(--font-en-serif)' }}>
                Lifting
              </h2>
              <div className="bx">
                <span className="text-[clamp(20px,3vw,28px)] text-white/90">리프팅</span>
              </div>
            </div>
            <div className="marquee absolute bottom-0 left-0 w-full h-20 overflow-hidden z-10">
              <div className="absol absolute flex whitespace-nowrap">
                <div className="mar_ch text-[clamp(40px,6vw,80px)] font-light text-white/20 tracking-wider" style={{ fontFamily: 'var(--font-en-grotesk)' }}>
                  HOMMAGE LIFTING
                </div>
              </div>
            </div>
          </div>

          {/* 섹션 3: 설명 */}
          <div
            className="sec3 relative h-screen w-screen flex-shrink-0 flex items-center justify-center overflow-hidden bg-cover bg-center bg-[#1a1a1a]"
            style={{
              backgroundImage: "url(https://cloud-1de12d.becdn.net/media/original/50f10464e3a42431e2bad159e0d83ec1/lifting0009.jpg)",
            }}
          >
            <div className="scrollcircle absolute top-20 left-1/2 -translate-x-1/2 z-10">
              <div className="rel relative">
                <img 
                  src="/asset/img/sub/scroll_circle.png" 
                  alt="scroll" 
                  className="w-16 h-16 opacity-50"
                  onError={(e) => {
                    (e.target as HTMLImageElement).style.display = "none";
                  }}
                />
                <span className="absolute inset-0 flex items-center justify-center">
                  <svg
                    id="arrow_down"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 128.66 218.52"
                    className="w-8 h-8"
                  >
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,408.4a61.33,61.33,0,0,1,61.33-61.33"
                      transform="translate(610.96 -192.89)"
                    />
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,195.89V408.4A61.33,61.33,0,0,0-608,347.07"
                      transform="translate(610.96 -192.89)"
                    />
                  </svg>
                </span>
              </div>
            </div>
            <div className="con absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 z-10 max-w-4xl px-8">
              <div className="tit mb-12">
                <span className="block text-[clamp(40px,6vw,60px)] font-light text-white mb-4">02</span>
                <h2 className="text-[clamp(32px,5vw,48px)] font-light text-white mb-6">
                  리프팅이란?
                </h2>
                <p className="text-[clamp(14px,1.5vw,18px)] text-white/90 leading-relaxed">
                  고주파, 초음파, 레이저, 실 등을 이용하여 탄력 있는 V라인을 만들기 위한 최적의 리프팅 컴비네이션.
                  <br />
                  각 리프팅 시술을 기술적으로 컴바인하여 단 1일만에 원하는 리프팅 효과를 나타낼 수 있는
                  <br />
                  원데이익스트림리프팅, 또는 과도한 지방을 분해하고 배출하여 윤곽을 개선해주는 조각주사와 함께 컴바인한
                  <br />
                  '조각술'은 더욱 효과적인 라인을 만들어주는 오마쥬만의 차별화된 특별한 경험이 될 것입니다.
                </p>
              </div>
              <div className="line w-full h-px bg-white/20 mb-8" />
              <div className="sub">
                <p className="text-[clamp(14px,1.5vw,18px)] text-white/90">
                  <b className="text-white">종류</b>
                  <span className="ml-4">
                    울쎄라, 슈링크 유니버스, 튠페이스, 실리프팅, V라인 리프팅, 이중턱 리프팅
                  </span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Fusion 섹션 */}
      <LiftingFusionBlock />

      {/* Speciality 섹션 */}
      <section className="speciality relative min-h-screen overflow-hidden bg-[#1a1a1a]">
        <div
          className="bg absolute inset-0 bg-cover bg-center bg-[#1a1a1a]"
          style={{
            backgroundImage: "url(https://aanclinic.com/img/main_img03.png)",
          }}
        />
        <div className="wrap relative z-10 max-w-[1600px] mx-auto px-[6vw] py-32">
          <div className="tit mb-20">
            <h2 className="text-[clamp(40px,6vw,60px)] font-light text-white text-center" style={{ fontFamily: 'var(--font-en-serif)' }}>Speciality</h2>
          </div>
          <div className="grid lg:grid-cols-2 gap-16 items-start">
            <div className="lbx">
              <p className="text-[clamp(24px,4vw,36px)] font-light text-white leading-relaxed">
                오마쥬 <span className="font-medium">리프팅</span>으로
                <br />
                변하지 않는 아름다움을
                <br />
                만들어 보세요.
              </p>
            </div>
            <div className="con">
              <div className="rbx grid md:grid-cols-2 gap-8">
                {SPECIALITY_ITEMS.map((item, idx) => (
                  <div key={item.id} className="bx bg-white/10 backdrop-blur-sm p-6 rounded-lg border border-white/20">
                    <div className="btn mb-4">
                      <span className="text-xs text-white/60 uppercase tracking-wider">{item.id}</span>
                    </div>
                    <div className="cont">
                      <h3 className="text-lg font-medium text-white mb-3">{item.title}</h3>
                      <p className="text-sm text-white/80 leading-relaxed">{item.description}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Feature 섹션 */}
      <section className="feature relative py-32 bg-white">
        <div className="wrap max-w-[1440px] mx-auto px-[6vw]">
          <div className="tit text-center mb-20">
            <h2 className="text-[clamp(40px,6vw,60px)] font-light text-[#111] mb-4" style={{ fontFamily: 'var(--font-en-serif)' }}>Feature</h2>
            <p className="text-[clamp(16px,2vw,20px)] text-gray-600">
              <span className="font-medium text-[#111]">리프팅</span> 시술별 특징
            </p>
          </div>
          <div className="con grid md:grid-cols-3 gap-8 mb-16">
            {FEATURE_ITEMS.map((item) => (
              <Link
                key={item.num}
                href={item.link}
                className="bx group bg-[#fafafa] p-8 rounded-lg border border-gray-200 hover:border-[var(--hm-gold)] transition-colors"
              >
                <div className="bxCont">
                  <div className="top mb-6">
                    <p className="text-xl font-medium text-[#111] mb-3">
                      <span className="num text-[var(--hm-gold)] mr-2">{item.num}</span>
                      {item.title}
                    </p>
                    <span className="text-sm text-gray-600 leading-relaxed block">{item.description}</span>
                  </div>
                  <div className="bottom">
                    <div className="view flex items-center gap-2 text-[var(--hm-gold)] opacity-0 group-hover:opacity-100 transition-opacity">
                      <span className="text-sm font-medium">View more</span>
                      <div className="arrow">
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                        </svg>
                      </div>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
          <div className="linkBx flex flex-wrap justify-center gap-4">
            {[
              { name: "조각주사", link: "#" },
              { name: "이지실리프팅", link: "#", active: true },
              { name: "티타늄리프팅", link: "#" },
              { name: "콰트로리프팅", link: "#" },
              { name: "원데이익스트림리프팅", link: "#" },
              { name: "B토닝", link: "#" },
            ].map((link) => (
              <Link
                key={link.name}
                href={link.link}
                className={`px-6 py-3 border border-gray-300 rounded-full text-sm transition-colors ${
                  link.active
                    ? "bg-[#111] text-white border-[#111]"
                    : "bg-white text-gray-700 hover:border-[#111]"
                }`}
              >
                {link.name}
              </Link>
            ))}
          </div>
        </div>
      </section>
    </main>
  );
}
